:root {
  color-scheme: light dark;
  color: #ffffffde;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #242424;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #0a0a0a 100%);
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  overflow-x: hidden;
}

#app {
  width: 100vw;
  min-height: 100vh;
}

.container {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

.hero-section {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

#scene-container {
  z-index: 1;
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.content {
  z-index: 10;
  text-align: center;
  pointer-events: none;
  padding: 2rem;
  position: relative;
}

.title {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px #646cff4d;
  background: linear-gradient(45deg, #646cff, #9f9fff) text;
  margin-bottom: .5rem;
  font-size: 4em;
  line-height: 1.1;
}

@keyframes glow {
  from {
    text-shadow: 0 0 30px #646cff4d;
  }

  to {
    text-shadow: 0 0 40px #646cff99, 0 0 60px #646cff4d;
  }
}

.subtitle {
  color: #fffc;
  text-shadow: 0 0 20px #fff3;
  margin-bottom: 1rem;
  font-size: 1.4em;
}

.scroll-hint {
  color: #fff9;
  text-shadow: 0 0 10px #ffffff4d;
  font-size: 1.1em;
  font-style: italic;
  animation: 2s ease-in-out infinite pulse;
}

@keyframes pulse {
  0%, 100% {
    opacity: .6;
  }

  50% {
    opacity: 1;
  }
}

.scroll-sections {
  z-index: 5;
  backdrop-filter: blur(10px);
  background: #0000004d;
  position: relative;
}

.resume-section {
  border-bottom: 1px solid #ffffff1a;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 6rem 2rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.resume-section:before {
  content: "";
  opacity: 0;
  pointer-events: none;
  background: radial-gradient(circle, #646cff1a 0%, #0000 70%);
  transition: opacity .5s;
  position: absolute;
  inset: 0;
}

.resume-section:hover:before {
  opacity: 1;
}

.section-content {
  z-index: 2;
  width: 100%;
  max-width: 1200px;
  position: relative;
}

.section-content h2 {
  text-align: center;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px #646cff4d;
  background: linear-gradient(45deg, #646cff, #9f9fff) text;
  margin-bottom: 3rem;
  font-size: 3em;
}

.about-content {
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.profile-info h3 {
  color: #646cff;
  text-shadow: 0 0 20px #646cff66;
  margin-bottom: .5rem;
  font-size: 2.5em;
}

.profile-info .title {
  color: #ffffffe6;
  text-shadow: 0 0 15px #fff3;
  margin-bottom: 2rem;
  font-size: 1.5em;
}

.profile-info .summary {
  color: #fffc;
  text-shadow: 0 0 10px #ffffff1a;
  font-size: 1.3em;
  font-style: italic;
  line-height: 1.8;
}

.skills-grid {
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  display: grid;
}

.skill-category h3 {
  color: #646cff;
  text-align: center;
  text-shadow: 0 0 15px #646cff4d;
  margin-bottom: 1.5rem;
  font-size: 1.5em;
}

.skill-tags {
  flex-wrap: wrap;
  justify-content: center;
  gap: .8rem;
  display: flex;
}

.skill-tag {
  color: #fff;
  backdrop-filter: blur(10px);
  background: linear-gradient(45deg, #646cff, #535bf2);
  border-radius: 25px;
  padding: .7rem 1.2rem;
  font-size: 1em;
  font-weight: 500;
  transition: all .3s;
  box-shadow: 0 4px 15px #646cff4d;
}

.skill-tag:hover {
  transform: translateY(-3px)scale(1.05);
  box-shadow: 0 8px 25px #646cff80;
}

.timeline {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.timeline-item:nth-child(odd) {
  flex-direction: row;
}

.timeline-item:nth-child(2n) {
  flex-direction: row-reverse;
}

.timeline-marker {
  z-index: 2;
  background: #646cff;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: 2s ease-in-out infinite pulse;
  position: absolute;
  top: 50%;
  left: 49%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 6px #646cff33, 0 0 20px #646cff80;
}

.timeline-content {
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  width: 45%;
  padding: 2rem;
  transition: all .3s;
  box-shadow: 0 8px 32px #0000004d;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px #646cff33;
}

.timeline-content h3 {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  margin-bottom: .5rem;
  font-size: 1.4em;
}

.timeline-content h4 {
  color: #ffffffe6;
  margin-bottom: .5rem;
  font-size: 1.2em;
}

.timeline-description {
  list-style: none;
}

.timeline-description li {
  color: #fffc;
  margin-bottom: .8rem;
  padding-left: 1.5rem;
  line-height: 1.6;
  position: relative;
}

.timeline-description li:before {
  content: "▸";
  color: #646cff;
  font-size: 1.2em;
  position: absolute;
  left: 0;
}

.education-grid {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  display: grid;
}

.education-item {
  backdrop-filter: blur(15px);
  text-align: center;
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 2.5rem;
  transition: all .3s;
  box-shadow: 0 8px 32px #0000004d;
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px #646cff33;
}

.education-item h3 {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  margin-bottom: .5rem;
  font-size: 1.4em;
}

.education-item h4 {
  color: #ffffffe6;
  margin-bottom: .5rem;
  font-size: 1.2em;
}

.education-meta {
  color: #fff9;
  margin-bottom: .5rem;
  font-size: .9em;
}

.gpa {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  font-size: .9em;
  font-weight: 600;
}

.projects-grid {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  display: grid;
}

.project-card {
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 2.5rem;
  transition: all .3s;
  box-shadow: 0 8px 32px #0000004d;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 50px #646cff4d;
}

.project-header {
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
  display: flex;
}

.project-header h3 {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  margin-bottom: .5rem;
  font-size: 1.4em;
}

.project-link {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  font-size: .9em;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.project-link:hover {
  color: #9f9fff;
}

.project-description {
  color: #fffc;
  margin-bottom: 1.5rem;
  font-size: 1.1em;
  line-height: 1.7;
}

.project-tech {
  flex-wrap: wrap;
  gap: .8rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.tech-tag {
  color: #646cff;
  background: #646cff33;
  border: 1px solid #646cff4d;
  border-radius: 20px;
  padding: .5rem 1rem;
  font-size: .9em;
  font-weight: 500;
}

.project-features {
  list-style: none;
}

.project-features li {
  color: #ffffffb3;
  margin-bottom: .8rem;
  padding-left: 1.8rem;
  font-size: 1em;
  line-height: 1.6;
  position: relative;
}

.project-features li:before {
  content: "✓";
  color: #646cff;
  font-size: 1.2em;
  font-weight: bold;
  position: absolute;
  left: 0;
}

.certifications-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  display: grid;
}

.certification-item {
  backdrop-filter: blur(10px);
  text-align: center;
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 12px;
  padding: 2rem;
  transition: all .3s;
}

.certification-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px #646cff33;
}

.certification-item h3 {
  color: #646cff;
  margin-bottom: .5rem;
  font-size: 1.2em;
}

.cert-issuer {
  color: #ffffffe6;
  margin-bottom: .5rem;
}

.cert-date {
  color: #fff9;
  margin-bottom: 1rem;
}

.cert-link {
  color: #646cff;
  font-size: .9em;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.cert-link:hover {
  color: #9f9fff;
}

.contact-info {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
  display: grid;
}

.contact-item {
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  transition: all .3s;
  display: flex;
  box-shadow: 0 8px 32px #0000004d;
}

.contact-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px #646cff33;
}

.contact-icon {
  filter: drop-shadow(0 0 10px #ffffff4d);
  min-width: 40px;
  font-size: 2em;
}

.contact-item a {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  font-size: 1.1em;
  text-decoration: none;
  transition: color .3s;
}

.contact-item a:hover {
  color: #9f9fff;
}

.contact-item span {
  color: #fffc;
  font-size: 1.1em;
}

@media (width <= 768px) {
  .title {
    font-size: 2.5em;
  }

  .section-content h2 {
    font-size: 2.2em;
  }

  .timeline:before {
    left: 20px;
  }

  .timeline-item {
    flex-direction: row !important;
  }

  .timeline-marker {
    left: 20px;
  }

  .timeline-content {
    width: calc(100% - 60px);
    margin-left: 60px;
  }

  .skills-grid, .education-grid, .projects-grid, .contact-info {
    grid-template-columns: 1fr;
  }
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #fff;
  }

  body {
    background: linear-gradient(135deg, #f5f5f5 0%, #e5e5e5 50%, #f5f5f5 100%);
  }

  .scroll-sections {
    background: #ffffff4d;
  }

  .resume-section {
    border-bottom: 1px solid #0000001a;
  }

  .section-content h2 {
    -webkit-text-fill-color: transparent;
    background: linear-gradient(45deg, #646cff, #535bf2) text;
  }

  .profile-info .summary, .project-description, .timeline-description li, .project-features li {
    color: #000c;
  }

  .timeline-content, .education-item, .project-card, .contact-item {
    background: #fff3;
    border: 1px solid #0000001a;
  }

  a:hover {
    color: #747bff;
  }

  button {
    background-color: #f9f9f9;
  }
}

.stylish-slideshow-trigger {
  cursor: pointer;
  background: linear-gradient(90deg, #23262f 0%, #deacf5 100%);
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 2px;
  margin: 32px 0 0;
  transition: box-shadow .3s, background .3s;
  display: flex;
  box-shadow: 0 2px 24px #00eaff33;
}

.stylish-slideshow-trigger:hover {
  background: linear-gradient(90deg, #deacf5 0%, #23262f 100%);
  box-shadow: 0 4px 32px #00eaff77;
}

.skills-dynamic-grid {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 60px;
  margin-top: 24px;
  padding: 20px 0;
  display: flex;
}

.skill-category {
  opacity: 0;
  background: #20222b;
  border-radius: 18px;
  flex: 260px;
  min-width: 220px;
  max-width: 320px;
  margin: 10px;
  padding: 32px 24px 24px;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  animation: .6s cubic-bezier(.4, 0, .2, 1) forwards slideInUp;
  position: relative;
  overflow: hidden;
  transform: translateY(20px);
  box-shadow: 0 2px 16px #00eaff11;
}

.skill-category:before {
  content: "";
  opacity: 0;
  background: linear-gradient(90deg, #deacf5 0%, #00eaff 100%);
  height: 2px;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.skill-category:hover:before {
  opacity: 1;
}

.skill-category:first-child {
  animation-delay: .1s;
}

.skill-category:nth-child(2) {
  animation-delay: .2s;
}

.skill-category:nth-child(3) {
  animation-delay: .3s;
}

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.skill-category:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 40px #00eaff44;
}

.skill-category h3 {
  color: #deacf5;
  text-align: center;
  margin: 0 0 18px;
  font-size: 1.2rem;
  font-weight: 600;
  position: relative;
}

.skill-category h3:after {
  content: "";
  background: linear-gradient(90deg, #deacf5 0%, #00eaff 100%);
  border-radius: 1px;
  width: 40px;
  height: 2px;
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
}

.skill-bars {
  margin-top: 18px;
}

.skill-bar-row {
  opacity: 0;
  z-index: 1;
  align-items: center;
  margin-bottom: 20px;
  animation: .5s cubic-bezier(.4, 0, .2, 1) forwards slideInLeft;
  display: flex;
  position: relative;
  transform: translateX(-20px);
}

.skill-bar-row:first-child {
  animation-delay: .4s;
}

.skill-bar-row:nth-child(2) {
  animation-delay: .5s;
}

.skill-bar-row:nth-child(3) {
  animation-delay: .6s;
}

.skill-bar-row:nth-child(4) {
  animation-delay: .7s;
}

.skill-bar-row:nth-child(5) {
  animation-delay: .8s;
}

.skill-bar-row:nth-child(6) {
  animation-delay: .9s;
}

@keyframes slideInLeft {
  0% {
    opacity: 0;
    left: -100vw;
    transform: translate(-50%, -50%)scale(.92);
  }

  100% {
    opacity: 1;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

.skill-bar-row:hover {
  transition: transform .2s;
  transform: scale(1.02);
}

.skill-bar-row:hover .skill-bar-fill {
  animation: 1s ease-in-out pulse;
}

.skill-bar-row:hover .skill-bar-glow {
  opacity: 1;
  animation: 2s ease-in-out infinite glow;
}

.skill-bar-label {
  color: #deacf5;
  z-index: 2;
  white-space: nowrap;
  background: none;
  flex: 0 0 110px;
  font-size: 1rem;
  font-weight: 600;
  transition: color .3s;
  position: relative;
}

.skill-bar-row:hover .skill-bar-label {
  color: #fff;
  text-shadow: 0 0 8px #deacf5;
}

.skill-bar-bg {
  background: linear-gradient(90deg, #23262f 0%, #2a2d38 100%);
  border-radius: 10px;
  flex: 1;
  min-width: 80px;
  height: 14px;
  margin: 0 12px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 4px #0000004d;
}

.skill-bar-fill {
  z-index: 1;
  background: linear-gradient(90deg, #deacf5 0%, #00eaff 50%, #deacf5 100%) 0 0 / 200% 100%;
  border-radius: 10px;
  width: 0%;
  height: 100%;
  transition: width 2s cubic-bezier(.4, 0, .2, 1);
  animation: 3s ease-in-out infinite shimmer;
  position: relative;
  box-shadow: 0 0 10px #00eaff44;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }

  100% {
    background-position: 200% 0;
  }
}

.skill-bar-glow {
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  background: linear-gradient(90deg, #0000 0%, #00eaff22 50%, #0000 100%);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.skill-bar-percentage {
  color: #00eaff;
  text-align: right;
  opacity: 0;
  flex: 0 0 40px;
  font-size: .9rem;
  font-weight: 600;
  transition: all .3s;
  transform: translateX(10px);
}

.skill-bar-row:hover .skill-bar-percentage {
  opacity: 1;
  text-shadow: 0 0 6px #00eaff;
  transform: translateX(0);
}

.timeline {
  border-left: 3px solid #deacf5;
  margin: 40px 0 0;
  padding-left: 40px;
  position: relative;
}

.timeline-item {
  align-items: center;
  margin-bottom: 48px;
  display: flex;
  position: relative;
}

.timeline-marker {
  z-index: 2;
  background: #181a20;
  border: 4px solid #deacf5;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 50%;
  left: -22px;
  transform: translateY(-50%);
  box-shadow: 0 2px 12px #deacf577;
}

.timeline-content {
  background: #20222b;
  border-radius: 14px;
  flex: 1;
  margin-left: 24px;
  padding: 22px 28px 18px;
  box-shadow: 0 2px 16px #00eaff11;
}

.timeline-content h3 {
  color: #deacf5;
  margin: 0 0 6px;
  font-size: 1.1rem;
  font-weight: 600;
}

.timeline-content h4 {
  color: #00eaff;
  margin: 0 0 8px;
  font-size: 1rem;
  font-weight: 500;
}

.timeline-meta {
  color: #bdbdbd;
  margin-bottom: 10px;
  font-size: .95rem;
}

.timeline-description {
  color: #fff;
  margin: 0 0 0 18px;
  padding: 0;
  font-size: .98rem;
}

.timeline-description li {
  margin-bottom: 6px;
}

.fullscreen-slideshow-modal {
  z-index: 99999;
  opacity: 0;
  pointer-events: none;
  background: #181a20f2;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.fullscreen-slideshow-modal.active {
  opacity: 1;
  pointer-events: all;
}

.slideshow-overlay {
  background: #181a20d9;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.slideshow-image, .slideshow-image-old {
  opacity: 0;
  z-index: 2;
  object-fit: contain;
  background: #181a20;
  border-radius: 24px;
  max-width: 90vw;
  max-height: 90vh;
  transition: opacity .7s, transform .7s cubic-bezier(.77, 0, .18, 1.01), left .7s, right .7s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.92);
  box-shadow: 0 2px 32px #00eaff77;
}

.slideshow-image.show {
  opacity: 1;
  transform: translate(-50%, -50%)scale(1);
}

.slide-in-right {
  opacity: 0;
  animation: .7s forwards slideInRight;
  left: 100vw;
}

.slide-in-left {
  opacity: 0;
  animation: .7s forwards slideInLeft;
  left: -100vw;
}

.slide-out-left {
  animation: .7s forwards slideOutLeft;
}

.slide-out-right {
  animation: .7s forwards slideOutRight;
}

@keyframes slideInRight {
  0% {
    opacity: 0;
    left: 100vw;
    transform: translate(-50%, -50%)scale(.92);
  }

  100% {
    opacity: 1;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

@keyframes slideOutLeft {
  0% {
    opacity: 1;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }

  100% {
    opacity: 0;
    left: -100vw;
    transform: translate(-50%, -50%)scale(.92);
  }
}

@keyframes slideOutRight {
